.folder-structure {
  width: 100%;
  height: 100vh;
  display: flex;
  .sidebar {
    min-width: 15%;
    padding: 12px;
    border-right: 1px solid gray;
    resize: horizontal;
    overflow: scroll;
    .folder {
      display: flex;
      align-items: center;
      cursor: pointer;
      .title {
        font-size: 12px;
        padding-left: 8px;
      }
      .fas {
        font-size: 1.5em;
        color: #56a5e0
      }
    }
    .file {
      padding: 2px 0px 2px 16px;
      position: relative;
      cursor: pointer;
      .title {
        font-size: 10px;
        padding-left: 8px;
      }
      .menu {
        width: 150px;
        position: absolute;
        top: 17px;
        left: 40px;
        border: 1px solid grey;
        background: #bbb8b845;
      }
    }
  }
  .empty-folder {
    padding-left: 24px;
    font-size: 8px;
  }
  .content {
    padding: 12px;
  }
}
